/**
 * Services component styling.
 */
/* stylelint-disable */
/* stylelint-enable */
.services-main-wrapper {
  background: linear-gradient(180deg, rgba(15, 25, 34, 0) 0%, #0F1922 31.4%);
  padding-block-end: 40px;
}

.services {
  margin-block: 3.5rem;
  padding-block-start: 3.5rem;
  display: flex;
  flex-wrap: wrap;
  column-gap: 50px;
}
.services-info {
  flex: 1 33.3%;
  align-self: center;
}
.services-info h2 {
  font-size: 2.25rem;
  line-height: 1.2;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #fff;
}
.services-info h4.subheading {
  font-size: 1.5rem;
  line-height: 1.2;
  margin-bottom: 2.4rem;
  font-weight: 400;
  color: #fff;
}
.services-info p {
  color: #fff;
  margin: 0 0 2.4rem 0;
  font-size: 1.25;
  font-weight: 400;
  line-height: 1.6;
  padding-inline-start: 1rem;
  border-left: 3px solid #0097FF;
}
.services-info .btn.btn-primary svg {
  padding-inline-start: 5px;
}
.services-card {
  flex: 0 58.33%;
}
@media (max-width: 768px) {
  .services-card {
    flex: 0 100%;
    margin-block-end: 1.5rem;
  }
}
.services-card .icon-cards {
  padding: 0;
  margin: 0;
}
.services-card .icon-cards .icon-card__wrappar {
  align-items: flex-start;
}
.services-card .icon-cards .icon-card {
  border: none;
}
.services-card .icon-cards .icon-card__body {
  padding-block: 0;
}
.services-card .icon-cards .icon-card__body p {
  margin-bottom: 1rem;
  color: #98928a;
  font-size: 1rem;
  line-height: 1.2;
}
.services-card .icon-cards .icon-card__body .btn {
  background-color: transparent;
}